// loader.interceptors.ts
import { Injectable, ɵConsole } from '@angular/core';
import {
    HttpErrorResponse,
    HttpResponse,
    HttpRequest,
    HttpHandler,
    HttpEvent,
    HttpInterceptor
} from '@angular/common/http';
import { Observable } from 'rxjs';
// import { LoaderService } from './loader.service';
import Swal from 'sweetalert2';
import * as CryptoJS from 'crypto-js';

@Injectable()
export class LoaderInterceptor implements HttpInterceptor {
    private requests: HttpRequest<any>[] = [];

    constructor() { }

    removeRequest(req: HttpRequest<any>) {
        const i = this.requests.indexOf(req);
        if (i >= 0) {
            this.requests.splice(i, 1);
        }
        // this.loaderService.isLoading.next(this.requests.length > 0);
    }
    intercept(req: HttpRequest<any>, next: HttpHandler): Observable<HttpEvent<any>> {
        this.requests.push(req);
        // console.log('No of requests--->' + this.requests.length);
        let encryptedAES = CryptoJS.AES.encrypt('sr', '@');
        let data = encryptedAES.toString();

        // const data = '12345';
        const modifiedReq = req.clone({
            headers: req.headers.set('Auth', data),
        });
        // console.log(modifiedReq, data);
        return next.handle(modifiedReq);
    }

    intercept1(req: HttpRequest<any>, next: HttpHandler): Observable<HttpEvent<any>> {

        this.requests.push(req);
        console.log('No of requests--->' + this.requests.length);
        // this.loaderService.isLoading.next(true);
        return Observable.create(observer => {
            const subscription = next.handle(req)
                .subscribe(
                    event => {
                        if (event instanceof HttpResponse) {
                            this.removeRequest(req);
                            observer.next(event);
                        }
                    },
                    err => {
                        //  alert('error returned');
                        console.log(err);

                        Swal.fire(' Http Exception Occur !');

                        this.removeRequest(req);
                        observer.error(err);
                    },
                    () => {
                        this.removeRequest(req);
                        observer.complete();
                    });
            // remove request from queue when cancelled
            return () => {
                this.removeRequest(req);
                subscription.unsubscribe();
            };
        });
    }
}
