import { Injectable } from "@angular/core";
import { HttpClient, HttpHeaders } from "@angular/common/http";
import { CreateRoleReqDTO } from './create-role-dto';
import { MedRolesPermission } from '../../new-role-list/roles-entities';
import { AllScreenPermission } from 'src/app/shared/models/dev-models/all-screen-permission';
import { AppConstants } from 'src/app/shared/config/app.constant';
import { RolesForUserRespDTO } from 'src/app/shared/models/dev-models/rolesForUserRespDTO';

//export const API_URL = "http://192.168.0.142:8010";
//export const API_URL = 'http://localhost:8010';
//export const API_URL = 'http://192.168.0.113:8010';
export const API_URL =  AppConstants.baseURL;


@Injectable({
  providedIn: "root"
})
export class RolesDevService {
  constructor(private http: HttpClient) { }
  getNavigationPermission(roleName: any) {
    return this.http.get<any>(
      `${API_URL}/rolePermission/getRoleForNav/${roleName}`
    );
  }

  getScreenPermissions(userId: string) {
    return this.http.get<RolesForUserRespDTO []>(
      `${API_URL}/roleNewImpl/getUserRolesPermission/${userId}`
    );
  }

  reOpenRole(currentUser: string, roleName: string) {
    return this.http.get<boolean>(
      `${API_URL}/roleNewImpl/reopenRole/${roleName}/${currentUser}`
    );
  }
  authorizeRole(currentUser: string, roleName: string) {
    return this.http.get<boolean>(
      `${API_URL}/roleNewImpl/authRole/${roleName}/${currentUser}`
    );
  }
  deleteRole(currentUser: string, roleName: string) {
    return this.http.get<boolean>(
      `${API_URL}/roleNewImpl/deleteRole/${roleName}/${currentUser}`
    );
  }
  closeRole(currentUser: string, roleName: string) {
    return this.http.get<boolean>(
      `${API_URL}/roleNewImpl/closeRole/${roleName}/${currentUser}`
    );
  }

  getMedRolesPermissionByRoleName(roleName: string) {
    return this.http.get<MedRolesPermission>(
      `${API_URL}/roleNewImpl/getMasterDataByRoleName/${roleName}`
    );
  }
  getRoleAuditLog(roleName: string) {
    // return this.http.get<RoleMasterObj>(
    //   `${API_URL}/roleNewImpl/getRoleAuditLog/${roleName}`
    // );
  }

  viewRoleDetailsByRoleName(roleName: string) {
    return this.http.get<AllScreenPermission>(
      `${API_URL}/roleNewImpl/getRoleByRoleName/${roleName}`    
    );
  }


  /*
   * common deupload api
   */
  // doExcelUploadService(excelDataProcessingReqDTO: ExcelDataProcessingReqDTO) {
  //   return this.http.post<boolean>(
  //     `${API_URL}/excelDataProcessing/doExcelUpload`,
  //     excelDataProcessingReqDTO
  //   );
  // }

  createRoleService(createRoleReqDTO: CreateRoleReqDTO) {
    return this.http.post<boolean>(
      `${API_URL}/roleNewImpl/createRole`,
      createRoleReqDTO
    );
  }
  modifyRoleService(createRolesReqDTO: CreateRoleReqDTO) {
    return this.http.post<boolean>(
      `${API_URL}/roleNewImpl/modifyRole`,
      createRolesReqDTO
    );
  }

  getAllRolesMasterData() {
    return this.http.get<MedRolesPermission>(
      `${API_URL}/roleNewImpl/getRolesSummary`
    );
  }
  getAllRolesDetailData() {
    return this.http.get<MedRolesPermission>(
      `${API_URL}/roleNewImpl/getRolesSummaryDetails`
    );
  }

  viewRoleDetails(roleId: number) {
    return this.http.get<AllScreenPermission>(
      `${API_URL}/roleNewImpl/getRole/${roleId}`
    );
  }
}
