import { ExcelDataProcessingAuditRespDTO } from 'src/app/views/excel-data-processing/excel-data-processing-dto';
import { FirstAndSecondAuthAuditLogRespDTO } from 'src/app/views/excel-data-authorization-first/audit-logs-dto-first-second';
