  //   );
  // }
  showDetailServiceInExcelDataProcessing(
    excelDataProcessingReqDTO: ExcelDataProcessingReqDTO, user_id
  ): Observable<any> {
    // return this.http.post(
    //   `${API_URL}/excelDataProcessing/showDetails/${user_id}`,
    //   excelDataProcessingReqDTO
    // );
    return this.http.post(
      `${API_URL}/excelDataProcessing/uploadExcelFile/${user_id}`,
      excelDataProcessingReqDTO
    );
  }
  showDetailServiceInExcelDataWithFile(
    file, extSysNameData, processNameData, filename
  ): Observable<any> {
    // return this.http.post(
    //   `${API_URL}/excelDataProcessing/getExcelFile`,file
    const formdata: FormData = new FormData();
    formdata.append('file', file);
    const req = new HttpRequest('POST', `${API_URL}/excelDataProcessing/getExcelFile/${extSysNameData}/${processNameData}/${filename}`, formdata, {
      reportProgress: true,
      responseType: 'text'
    }
    );
    return this.http.request(req);

  }
  processDataService(modalProcessDataReqDTO, userName: string, remarks: string) {
    // return this.http.post<boolean>(
    //   `${API_URL}/excelDataForProcessScreeen/processData/${userName}/${remarks}`,
    //   modalProcessDataReqDTO
    // );
    return this.http.post<boolean>(
      `${API_URL}/excelDataForProcessScreeen/processExcelData/${userName}/${remarks}`,
      modalProcessDataReqDTO
    );
  }

