import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { AuthComponent } from './shared/components/layout/auth/auth.component';
import { AdminComponent } from './shared/components/layout/admin/admin.component';
import { AuthGuard } from './shared/services/auth.guard';


export const rootRoutesConfig: Routes = [
  {
    path: '',
    redirectTo: 'login',
    pathMatch: 'full'
  },
  {
    path: '',
    component: AuthComponent,
    children: [
      {
        path: 'login',
        loadChildren: './views/login/login.module#LoginModule',
        data: { title: 'Login' ,preload: true }
      },
      {
        path: 'forget-password',
        loadChildren: './views/forget-password/forget-password.module#ForgetPasswordModule',
        data: { title: 'forget-password' }
      }

    ]
  },

  {
    path: '',
    component: AdminComponent,
    canActivate: [AuthGuard],
    children: [
      {
        path: 'dashboard',
        loadChildren: './views/dashboard/dashboard.module#DashboardModule',
        data: { title: 'Dashboard' }
      },

      {
        path: 'policy',
        loadChildren: './views/security-policy/security-policy.module#SecurityPolicyModule',
        data: { title: 'Security Policy' }
      },
      {
        path: 'process-monitor',
        loadChildren: './views/security-policy/change-password/change-password.module#ChangePasswordModule',
        data: { title: 'Process Monitoring' }
      },
      {
        path: 'transaction-group-maintenance',
        loadChildren: './views/transaction-group-maintenance/transaction-group-maintenance.module#TransactionGroupMaintenanceModule',
        data: { title: 'Transaction-Group-Maintenance' }

      },
      {
        path: 'global-gl-acc-maintenance',
        loadChildren: './views/transaction-group-maintenance/global-gl-ac-code-maint-summary/glMaintenanceModule.module#GlMaintenanceModule',
        // loadChildren: './views/global-gl-ac-code-maint-summary/glMaintenanceModule.module#GlMaintenanceModule',
        data: { title: 'Global-Transaction-Group-Maintenance' }

      },
      {
        path: 'user-gl-acc-maintenance',
        loadChildren: './views/transaction-group-maintenance/user-gl-ac-code-maint-summary/userGlMaintenanceModule.module#UserGlMaintenanceModule',
        data: { title: 'User-Transaction-Group-Maintenance' }

      },
      {
        path: 'excel-data-authorization-first',
        loadChildren: './views/excel-data-authorization-first/excel-data-authorization-first.module#ExcelDataAuthorizationFirstModule',
        data: { title: 'excel-data-authorization-first' }
      },
      {
        path: 'excel-data-authorization-second',
        loadChildren: './views/excel-data-authorization-second/excel-data-authorization-second.module#ExcelDataAuthorizationSecondModule',
        data: { title: 'excel-data-authorization-second' }
      },
      {
        path: 'excel-data-processing',
        loadChildren: './views/excel-data-processing/excel-data-processing.module#ExcelDataProcessingModule',
        data: { title: 'excel-data-processing' }
      },
      {
        path: 'data-modification-detail',
        loadChildren: './views/data-modification-detail/data-modification-detail.module#DataModificationDetailModule',
        data: { title: 'DataModificationDetail' }
      },
      {
        path: 'excel-data-process-summary',
        loadChildren: './views/excel-data-process-summary/excel-data-process-summary.module#ExcelDataProcessSummaryModule',
        data: { title: 'excel-data-process-summary' }
      },
      /* {
         path: 'roles',
         loadChildren: './views/roles-impl/roles-impl.module#RolesImplModule',
         data: { title: 'Roles' }
       }, */
      {
        path: 'roles1',
        loadChildren: './views/roles1/roles1.module#Roles1Module',
        data: { title: 'New Roles' }
      },
      {
        path: 'users',
        loadChildren: './views/users/users.module#UsersModule',
        data: { title: 'Users' }
      },
      {
        path: 'audit-logs',
        loadChildren: './views/audit-logs/audit-logs.module#AuditLogsModule',
        data: { title: 'Audit Logs' }
      },
      {
        path: 'account-block',
        loadChildren: './views/account-blocking/account-blocking.module#AccountBlockingModule',
        data: { title: 'Account-block' }
      },
      {
        path: 'email-summary',
        loadChildren: './views/email-maintenance/email-maintenance.module#EmailMaintenanceModule',
        data: { title: 'Account-block' }
      },
      {
        path: 'account-closure',
        loadChildren: './views/account-closure/account-closure.module#AccountClosureModule',
        data: { title: 'Account-closure' }

      },
      {
        path: 'account-closure-maintenance',
        loadChildren: './views/account-closure-maintenance/account-closure-maintenance.module#AccountClosureMaintenanceModule',
        data: { title: 'Account-closure' }
      },
      {
        path: 'charge-maintenance',
        loadChildren: './views/charge-maintenance/charge-maintenance.module#ChargeMaintenanceModule',
        data: { title: 'charge-maintenance' }

      },
      {
        path: 'process-account-closure',
        loadChildren: './views/process-account-closure/process-account-closure.module#ProcessAccountClosureModule',
        data: { title: 'process-account-closure' }

      },
      //vidya added for accumen Journal
      {
        path: 'fcy-deal-Number',
        loadChildren: './views/fcy-deal-number/fcy-deal-number.module#FcyDealNumberModule',
        data: { title: 'FcyDealNumberModule' }

      },
      //vidya added for Department Maintenenace and sftp amintenance
      {
        path: 'maintenance',
        loadChildren: './views/maintenance/maintenance.module#MaintenanceModule',
        data: { title: 'MaintenanceModule' }

      },
      {
        path: 'sias-v2',
        loadChildren: './views/sias-v2/sias-v2.module#SiasV2Module',
        data: { title: 'Excel' }
      },
      {
        path: 'new-ext',
        loadChildren: './views/new-external-system/new-external-system.module#NewExternalSystemModule',
        data: { title: 'Excel' }
      },
      {
        path: 'tags',
        loadChildren: './views/tags/tags.module#TagsModule',
        data: { title: 'Excel' }
      }
    ]
  },
  {
    path: '**',
    redirectTo: ''
  }
]
@NgModule({
  imports: [RouterModule.forRoot(rootRoutesConfig)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
