import { Component, OnInit } from '@angular/core';
import { MatDialog } from '@angular/material/dialog';
import { MatSnackBar } from '@angular/material/snack-bar';
import { MatTableDataSource } from '@angular/material/table';
import { RolesDevService } from '../roles-impl/new-role/role-dev-service';
import { MedRolesPermission } from './roles-entities';
import { ModalRoleViewComponent } from '../modals/modal-role-view/modal-role-view.component';

@Component({
  selector: 'app-new-role-list',
  templateUrl: './new-role-list.component.html',
  styleUrls: ['./new-role-list.component.css']
})
export class NewRoleListComponent implements OnInit {

  public allRoles: any;

  constructor(
    private api: RolesDevService,
    public dialog: MatDialog,
    private snackBar: MatSnackBar
  ) { }

  dataSourceAllRoles = new MatTableDataSource<MedRolesPermission>(
    this.allRoles
  );

  displayedColumns: string[] = [
    'roleName',
    'roleDesc',
    'process'
  ];

  ngOnInit() {
    this.getrolesList();
  }

  getrolesList() {
    this.api.getAllRolesMasterData().subscribe(resp => {
      ////console.log(resp);
      this.allRoles = resp;
      //console.log('all roles below');
      //console.log(this.allRoles);
      this.dataSourceAllRoles = new MatTableDataSource<MedRolesPermission>(
        this.allRoles
      );
    });
  }

  queryRoleDetails(medRolesPermission: MedRolesPermission) {
  }
  
  openDialog(medRolesPermission: MedRolesPermission) {
    const dialogRef = this.dialog.open(ModalRoleViewComponent, {
      data: medRolesPermission
    });
    dialogRef.afterClosed().subscribe(result => {
      //console.log(`Dialog result: ${result}`);
    });
  }
}
