import { Component, OnInit, ViewChild, Inject } from '@angular/core';
import { MatPaginator } from '@angular/material/paginator';
import { MAT_DIALOG_DATA, MatDialog } from '@angular/material/dialog';
import { MatSnackBar } from '@angular/material/snack-bar';
import { MedRolesPermission } from '../../new-role-list/roles-entities';
import { RolesDevService } from '../../roles-impl/new-role/role-dev-service';
import { AllScreenPermission } from 'src/app/shared/models/dev-models/all-screen-permission';
import { UserScreenPermission } from 'src/app/shared/models/dev-models/user-screen-permission';
import { RoleScreenPermission } from 'src/app/shared/models/dev-models/role-screen-permission';
import { ButtonPermission } from 'src/app/shared/models/dev-models/buttonPermission';
import { AuditLogScreenPermission } from 'src/app/shared/models/dev-models/audit-log-screen-permission';
// tslint:disable-next-line: max-line-length
import { ExcelDataModificationScreenPermission, ExcelProcessingSummaryScreenPermission } from 'src/app/shared/models/dev-models/median-screens-permission';

@Component({
  selector: 'app-modal-role-view',
  templateUrl: './modal-role-view.component.html',
  styleUrls: ['./modal-role-view.component.css']
})
export class ModalRoleViewComponent implements OnInit {

  @ViewChild(MatPaginator, { static: true }) paginator: MatPaginator;
  hahaha: boolean;

  adminScreenPermission = true;
  medianScreenPermission = false;

  allScreenPermission = new AllScreenPermission();
  buttonPermission = new ButtonPermission();
  roleScreenPermission = new RoleScreenPermission();
  userScreenPermission = new UserScreenPermission();
  auditLogScreenPermission = new AuditLogScreenPermission();
  eDMScreenPermission = new ExcelDataModificationScreenPermission();
  ePSScreenPermission = new ExcelProcessingSummaryScreenPermission();

  roleName: string;
  roleDesc: string;

  pendingForAuthDataDetails: any;

  medRolesPermission: MedRolesPermission;
  constructor(
    @Inject(MAT_DIALOG_DATA) private data,
    private api: RolesDevService,
    public dialog: MatDialog,
    private snackBar: MatSnackBar
  ) {}

  ngOnInit() {
    this.roleScreenPermission.buttonPermission = this.buttonPermission;
    this.userScreenPermission.buttonPermission = this.buttonPermission;
    this.auditLogScreenPermission.buttonPermission = this.buttonPermission;
    this.eDMScreenPermission.buttonPermission = this.buttonPermission;
    this.ePSScreenPermission.buttonPermission = this.buttonPermission;

    this.allScreenPermission.roleScreenPermission = this.roleScreenPermission;
    this.allScreenPermission.userScreenPermission = this.userScreenPermission;
    this.allScreenPermission.auditLogScreenPermission = this.auditLogScreenPermission;
    this.allScreenPermission.eDMScreenPermission = this.eDMScreenPermission;
    this.allScreenPermission.ePSScreenPermission = this.ePSScreenPermission;

    this.initializeButtons();
    this.showRoleDetails();
  }

  initializeButtons() {}

  showRoleDetails() {
    this.medRolesPermission = this.data;
    //console.log(this.medRolesPermission);
    this.roleName = this.medRolesPermission.id.roleName;
    this.roleDesc = this.medRolesPermission.roleDesc;
    this.api
      .viewRoleDetails(this.medRolesPermission.id.roleId)
      .subscribe(resp => {
        this.allScreenPermission = resp;
        //console.log('allScreenPermission');
        //console.log(this.allScreenPermission);
        this.hahaha = this.allScreenPermission.userScreenPermission.buttonPermission.newButton;
        // //console.log(
        //   this.allScreenPermission.userScreenPermission.buttonPermission
        //     .newButton
        // );
        //
        this.roleScreenPermission = this.allScreenPermission.roleScreenPermission;
        this.userScreenPermission = this.allScreenPermission.userScreenPermission;
        this.auditLogScreenPermission = this.allScreenPermission.auditLogScreenPermission;
        this.eDMScreenPermission = this.allScreenPermission.eDMScreenPermission;
        this.ePSScreenPermission = this.allScreenPermission.ePSScreenPermission;
      });
  }
  changeAdmin() {
    this.adminScreenPermission = true;
    this.medianScreenPermission = false;
  }
  changeMedian() {
    this.adminScreenPermission = false;
    this.medianScreenPermission = true;
  }
}
