import { PendingForAuthDetailsDTO, PendingForAuthDetailsHistEntity } from 'src/app/shared/models/pending-for-auth-datails-dto';
import { MAT_DIALOG_DATA, MatSnackBar } from '@angular/material';
import {
  Component,
  OnInit,
  QueryList,
  ViewChildren,
  Inject,
  ViewChild
} from '@angular/core';
import { MatPaginator } from '@angular/material/paginator';
import { MatTableDataSource } from '@angular/material/table';
import { FormControl } from '@angular/forms';
import { MatDialog } from '@angular/material/dialog';
import { ApiService } from 'src/app/shared/services/api.service';
import Swal from 'sweetalert2';
import { User } from 'src/app/shared/models/user';
import { ModalExcelAuthFirstComponent } from '../modal-excel-auth-first/modal-excel-auth-first.component';

@Component({
  selector: 'app-modal-audit-transaction-details',
  templateUrl: './modal-audit-transaction-details.component.html',
  styleUrls: ['./modal-audit-transaction-details.component.css']
})
export class ModalAuditTransactionDetailsComponent implements OnInit {

  @ViewChild(MatPaginator, { static: true }) paginator: MatPaginator;

  tempPendingForAuthDetailsHistEntity: PendingForAuthDetailsHistEntity = new PendingForAuthDetailsHistEntity();
  responseDtoList: PendingForAuthDetailsHistEntity[];

  constructor(@Inject(MAT_DIALOG_DATA) private data, private api: ApiService,
    public dialog: MatDialog) { }

  dataSource = new MatTableDataSource<PendingForAuthDetailsHistEntity>(
    this.responseDtoList
  );
  displayedColumns: string[] = [
    'versionNo',
    'account',
    'ccyCd',
    'branchCode',
    'amount',
    'lcyEquivalent',
    'drCr',
    'exchRate',
    'relatedAccount',
    'relCust',
    'addlText',
    'view'
  ];

  ngOnInit() {
    //console.log("inside modal  ");
    this.responseDtoList = this.data;
    //console.log(this.responseDtoList);
    this.dataSource = new MatTableDataSource<PendingForAuthDetailsHistEntity>(
      this.responseDtoList
    );
    this.dataSource.paginator = this.paginator;
  }
  // getAuditLogData(pendingForAuthDetailsHistEntity: PendingForAuthDetailsHistEntity) {
  //   this.tempPendingForAuthDetailsHistEntity = pendingForAuthDetailsHistEntity;
  // }
  openDialog(pendingForAuthDetailsHistEntity: PendingForAuthDetailsHistEntity) {
    this.tempPendingForAuthDetailsHistEntity = pendingForAuthDetailsHistEntity;
    //
    const dialogRef = this.dialog.open(ModalExcelAuthFirstComponent, {
      data: this.tempPendingForAuthDetailsHistEntity
    });

    dialogRef.afterClosed().subscribe(result => {
      //console.log(`Dialog result: ${result}`);
    });
  }
  openDialogAudit(pendingForAuthDetailsHistEntity: PendingForAuthDetailsHistEntity){
    this.tempPendingForAuthDetailsHistEntity = pendingForAuthDetailsHistEntity;
  }

}
