import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';

import { rootRoutesConfig } from './app-routing.module';
import { AppComponent } from './app.component';
import { SharedModule } from './shared/shared.module';
import { ServiceService } from './shared/services/service.service';
import { RouterModule } from '@angular/router';
import { BrowserAnimationsModule, NoopAnimationsModule } from '@angular/platform-browser/animations';
import { ApiService } from './shared/services/api.service';
import { HttpClientModule, HTTP_INTERCEPTORS } from '@angular/common/http';
import { ModalExcelAuthFirstComponent } from './views/modals/modal-excel-auth-first/modal-excel-auth-first.component';
import { NewRoleListComponent } from './views/new-role-list/new-role-list.component';
import { ModalRoleViewComponent } from './views/modals/modal-role-view/modal-role-view.component';
import { ReactiveFormsModule } from '@angular/forms';
import { ToastrModule } from 'ngx-toastr';
import { AppConfirmService } from './shared/services/app-confirm/app-confirm.service';
import { AuthService } from './shared/services/auth.service';
import { ModalAuditTransactionDetailsComponent } from './views/modals/modal-audit-transaction-details/modal-audit-transaction-details.component';
import { ChartsModule } from 'ng2-charts';
import { EncrDecrService } from './shared/enc/EncrDecrService.service';
import { DatePipe } from '@angular/common';
import { LoaderInterceptor } from './shared/services/loader.interceptor';
import { TimepickerModule } from 'ngx-bootstrap/timepicker';
import { BsModalService, BsModalRef } from 'ngx-bootstrap/modal';
import { ButtonsModule } from 'ngx-bootstrap/buttons';
import { QuicklinkModule, QuicklinkStrategy } from 'ngx-quicklink';
import { AppComfirmComponent } from './shared/services/app-confirm/app-confirm.component';


@NgModule({
  declarations: [
    AppComponent, ModalExcelAuthFirstComponent,
    NewRoleListComponent, ModalRoleViewComponent,
    ModalAuditTransactionDetailsComponent,
    AppComfirmComponent
  ],
  entryComponents: [ModalExcelAuthFirstComponent,
    ModalRoleViewComponent,
    ModalAuditTransactionDetailsComponent, AppComfirmComponent],
  // tslint:disable-next-line: max-line-length

  imports: [
    BrowserModule,
    NoopAnimationsModule,
    BrowserAnimationsModule,
    ReactiveFormsModule,
    SharedModule,
    QuicklinkModule,
    HttpClientModule,
    ToastrModule.forRoot(),
    ChartsModule,
    TimepickerModule.forRoot(),
    RouterModule.forRoot(rootRoutesConfig, { useHash: true,preloadingStrategy:QuicklinkStrategy})
  ],
  providers: [ServiceService, ApiService, AppConfirmService, AuthService, EncrDecrService, DatePipe,
    // LoaderService,
    { provide: HTTP_INTERCEPTORS, useClass: LoaderInterceptor, multi: true }],
  bootstrap: [AppComponent]
})
export class AppModule { }
